import axios from "axios";
import { emailGroup, email, password, result, middleInputsHolder, loginButton, loader } from "../modules/signup";

export const handleLogin = () => {
  loginButton.disabled = true;
  loginButton.innerText = "";
  loginButton.appendChild(loader);

  axios.post('https://api.cashly.me/enroll/', {
      email: email.value,
      password: password.value,
    })
    .then(response => {
      const data = response.data;
      result.innerText = "";
      result.style.color = '';
      if (data.response.code === 0) {
        result.innerText = data.response.payload.message;
        result.style.color = 'red';
      } else {
        result.innerText = "Signup successful! Redirecting to login...";
        result.style.color = 'green';
        middleInputsHolder.insertBefore(result, emailGroup);
        setTimeout(() => {
          window.location.href = '/login'; 
        }, 2000); 
      }
      setTimeout(() => {
        result.innerText = ""; 
        result.style.color = ''; 
        middleInputsHolder.removeChild(result);
      }, 5000);
    })
    .catch(error => {
      let errorMessage = "An unexpected error occurred.";
      if (error.response) {
        errorMessage = error.response.data.response.payload.message || errorMessage;
      }
      result.innerText = errorMessage;
      result.style.color = 'red';
      middleInputsHolder.insertBefore(result, emailGroup);
      setTimeout(() => {
        result.innerText = "";
        result.style.color = ''; 
        middleInputsHolder.removeChild(result);
      }, 5000);
    })
    .finally(() => {
      loginButton.disabled = false;
      loginButton.innerHTML = 'Signup';
    });
};
